import { useRef } from "react";

const DynamicGrid = () => {
  const r = useRef();
  return (
    <div>
      <button
        onClick={() => {
          console.log("click");
          r.current.click();
        }}
      >
        Upload
      </button>
      <input type="file" ref={r} hidden />
    </div>
  );
};
export default DynamicGrid;
